import React, { useState, useEffect } from 'react';
import { Route, Switch, Redirect } from "react-router-dom";
import { Routes } from "../routes";

// pages
import Home from "./Home";

import Channels from "./Channels";
import About from "./About";
import Request from "./Request";
import Advertisement from "./Advertisement";
import Token from "./Token";
import FreePriceBot from "./FreePriceBot";
import Voting from "./Voting";

import VIP from "./VIP";
import Roadmap from "./Roadmap";
import Term from "./Term";

import NotFoundPage from "./NotFound";
import ServerError from "./examples/ServerError";


// components
import Sidebar from "../components/Sidebar";
import Preloader from "../components/Preloader";

import useGaTracker from '../useGaTracker'


const RouteWithLoader = ({ component: Component, ...rest }) => {
  useGaTracker();
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => setLoaded(true), 1000);
    return () => clearTimeout(timer);
  }, []);

  return (
    <Route {...rest} render={props => ( <> <Preloader show={loaded ? false : true} /> <Component {...props} /> </> ) } />
  );
};

const RouteWithSidebar = ({ component: Component, ...rest }) => {
  useGaTracker();
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => setLoaded(true), 1000);
    return () => clearTimeout(timer);
  }, []);


  return (
    <Route {...rest} render={props => (
      <>
        <Preloader show={loaded ? false : true} />
        <Sidebar />

        <main className="content">
          <Component {...props} />
        </main>
      </>
    )}
    />
  );
};

export default () => (
  <Switch>
    <RouteWithLoader exact path={Routes.Home.path} component={Home} />
    <RouteWithLoader exact path={Routes.NotFound.path} component={NotFoundPage} />
    <RouteWithLoader exact path={Routes.ServerError.path} component={ServerError} />

    {/* pages */}
    <RouteWithSidebar exact path={Routes.Channels.path} component={Channels} />
    <RouteWithSidebar exact path={Routes.About.path} component={About} />
    <RouteWithSidebar exact path={Routes.RequestBot.path} component={Request} />
    <RouteWithSidebar exact path={Routes.Advertisement.path} component={Advertisement} />
    <RouteWithSidebar exact path={Routes.VIP.path} component={VIP} />
    <RouteWithSidebar exact path={Routes.Token.path} component={Token} />
    <RouteWithSidebar exact path={Routes.FreePriceBot.path} component={FreePriceBot} />
    <RouteWithSidebar exact path={Routes.Voting.path} component={Voting} />
    <RouteWithSidebar exact path={Routes.Roadmap.path} component={Roadmap} />
    <RouteWithSidebar exact path={Routes.Term.path} component={Term} />
    <Redirect to={Routes.NotFound.path} />
  </Switch>
);
